/**
 * Dao
 */
import axios from "axios";
import { _platformLibs } from "./platformLib";

class Dao {
  constructor({
    product,
    serverUrl,
    actionMap,
    csrfToken = "mc_gtk",
    loginCode = 1
  }) {
    this._product = product;
    this._actionMap = actionMap;

    const mergeParams = config => {
      let params = config["params"] || {};
      params._ = Date.now();
      params[csrfToken] = _platformLibs["util"] && _platformLibs["util"].getACSRFToken();

      config["params"] = params;

      return config;
    };

    const fetchApi = (this._fetchApi = axios.create({
      baseURL: serverUrl,
      withCredentials: true,
      _showLoading: true
    }));

    // 
    fetchApi.interceptors.request.use(
      config => {
        mergeParams(config);

        if (config._showLoading) {
          _platformLibs["nmc"] && _platformLibs["nmc"].requestStart();
        }
        return config;
      },
      error => {
        return Promise.reject(error);
      }
    );

    fetchApi.interceptors.response.use(
      response => {
        _platformLibs["nmc"] && _platformLibs["nmc"].requestStop();

        let { code } = response.data;

        // 
        if (code === loginCode) {
          _platformLibs["login"].show();
          return Promise.reject({ msg: "NEED_LOGIN" });
        }

        return response;
      },
      error => {
        _platformLibs["nmc"] && _platformLibs["nmc"].requestStop();
        return Promise.reject(error);
      }
    );
  }

  async get(_action, _data = {}, options = {}) {
    options.params = _data;
    let result = await this._fetchApi.get(this._actionMap[_action], options);
    return result.data;
  }

  async post(_action, _data = {}, options = {}) {
    let result = await this._fetchApi.post(
      this._actionMap[_action],
      _data,
      options
    );
    return result.data;
  }

  async request(options = {}) {
    let result = await this._fetchApi.request(options);
    return result.data;
  }

  async capi(cmd, data, { product, regionId = 1, ...args }) {
    const result = await _platformLibs["capi"].request(
      {
        serviceType: product || this._product,
        cmd: cmd,
        data: data,
        regionId: regionId
      },
      {
        secure: true,
        version: 3,
        tipErr: false,
        ...args
      }
    );

    return result.data.Response;
  }
}

export { Dao };
