/**
 * 
 */
import { _Vue, install } from "./install";
import { Directives } from "./directives";
import View from "./components/view";
import { PlatformLib, getPlatformLib } from "./platformLib";
import { Dao } from "./dao";

/**
 * Main entry
 */
class AppInit {
  constructor({
    product,
    productName,
    App,
    menu,
    routes,
    store,
    cssPath,
    isDebug,
    useRemoteQcVue,
    onInit,
    onRoute,
    onDestroy,
    platformLibs
  }) {
    product = product || menu.uri;
    productName = productName || menu.title;
    platformLibs = platformLibs || [];

    // 
    let platformLibsInstance = new PlatformLib(platformLibs);

    // 
    new Directives({
      platformLibs: platformLibsInstance
    });

    // 
    _Vue.component("RouterView", View);
  }
}

export default {
  install
};

export { AppInit, Dao, getPlatformLib };
