export default {
  name: "RouterView",
  functional: true,
  props: {
    name: {
      type: String,
      default: "default"
    }
  },
  render(_, { props, children, parent, data }) {
    // used by devtools to display a router-view badge
    data.routerView = true;

    const h = parent.$createElement;
    const name = props.name;
    const router = parent.$router;

    let depth = 0;
    while (parent && !parent._isRouterRoot) {
      if (parent.$vnode && parent.$vnode.data.routerView) {
        depth++;
      }
      parent = parent.$parent;
    }
    data._routerViewDepth = depth;

    const matched = router.matched[depth];
    if (!matched) {
      return h(
        {
          name: "empty-page",
          render(h) {
            return "";
          }
        },
        data,
        children
      );
    }

    const page = matched.pages[name];
    return h(page, data, children);
  }
};
