/**
 * PlatformLib
 */
import { _Vue } from "./install";
import { seajs } from './sea';

let _platformLibs = {};
let defaultPlatformLibsList = [
  { path: "nmc", key: "nmc", global: true },
  { path: "nmc/main/datamanager", key: "datamanager" },
  { path: "manager", key: "manager" },
  { path: "nmcConfig", key: "nmcConfig" },
  { path: "config/jsRouter", key: "jsRouter" },
  { path: "router", key: "router" },
  { path: "util", key: "util" },
  { path: "login", key: "login" },
  { path: "tips", key: "tips" },
  { path: "reporter", key: "reporter" },
  { path: "models/api", key: "capi" }
];
//  platformLibs 
defaultPlatformLibsList.forEach(item => {
  _platformLibs[item.key] = seajs.require(item.path);
});
class PlatformLib {
  constructor(platformLibs) {
    defaultPlatformLibsList = defaultPlatformLibsList.concat(platformLibs);

    //  platformLibs 
    defaultPlatformLibsList.forEach(item => {
      if (item.global) {
        this[item.key] = window[item.path];
      } else {
        this[item.key] = _platformLibs[item.key] = seajs.require(item.path);
      }
    });

    Object.defineProperty(_Vue.prototype, "$platformLibs", {
      get() {
        return this.$root._platformLibs;
      }
    });

    // beforeCreate mixin
    _Vue.mixin({
      beforeCreate() {
        //  platformLibs
        if (this.$options.platformLibs) {
          //  _platformLibs
          this._platformLibs = this.$options.platformLibs;
        }
      }
    });
  }
}

function getPlatformLib(name) {
  return _platformLibs[name];
}

export { PlatformLib, getPlatformLib, _platformLibs };
